.body{
    margin-top: 47px;
    height: 600px;
    background: url("https://img.freepik.com/premium-photo/robot-man-sit-front-computer-screen_806833-389.jpg?size=626&ext=jpg&ga=GA1.1.1412664699.1685573849&semt=sph");
    background-repeat: no-repeat;
    background-blend-mode: darken;
    background-size: cover;
}


.chaticon{
    display: block;
    position: fixed;
    right: 60px;
    bottom: 60px;
    color: white;
}

.hide{
    display: none;
}

.chaticon:hover{
    background-color: rgb(11, 80, 80);
}

.chaticon .iconchat{
    width: 50px;
    height: 50px;
    padding: 20px;
}

.chaticon{
    border: 2px solid white;
    border-radius: 50%;
}

.chatbox{
    border: 1px solid black;
    width: 300px;
    height: 500px;
    position: fixed;
    right: 40px;
    top: 100px;
    border-radius: 10px;
    z-index: 10;
}

.top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    height: 50px;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    border: 1px solid black;
    background-color: rgb(0, 0, 0);
    color: white;
}


.middle{
    height: 377px;
    background-color: rgb(237, 237, 237);
    overflow: scroll;
}

.buttom{
    height: 50px;
    /* border: 1px solid black; */
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    display: flex;
    align-items: center;
    border-left: none;
    border-right: none;
}

.buttom input{
    width: 245px;
    height: 45px;
    border-bottom-left-radius: 10px;
    outline: none;
    font-size: 20px;
    border: none;
}

.buttom input::placeholder{
    outline: none;
    height: 45px;
    border: none;
}

.send{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 47px;
    height: 47px;
    background-color: white;
}

.sendd{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 47px;
    border-bottom-right-radius: 10px;
    background-color: white;
}

.message{
    display: flex;
    flex-wrap: wrap;
    max-width: 300px;
    height: 30px;
    padding: 5px;
    position: relative;
    overflow: scroll;
}

.messageu{
    background-color: rgb(61, 137, 238);
}

.App{
    margin-top: 100px;
}

.btn{
    margin-top: 100px;

}

.audio{
    position: fixed;
    bottom: 0;
    left: 590px;
}

.btn{
    position: fixed;
    bottom: 10px;
    left: 605px;
}